<template>
   <div>
      <Row>
         <Col>
            <img src="../assets/one.png" width="200px" />
            <Divider>
               <h3>
                  
               </h3>
            </Divider>
         </Col>
      </Row>

      <Row v-if="show">
         <Row>
            <Col span="24">
               <Table
                  border
                  :columns="columns12"
                  :data="allItems"
                  style="direction:rtl"
               >
                  <template slot-scope="{ row }" slot="name">
                     <strong>{{ row.name }}</strong>
                  </template>
                  <template slot-scope="{ row, index }" slot="action">
                     <Button
                        type="primary"
                        size="small"
                        style="margin-left: 5px"
                        @click="goToComments(row)"
                        icon="ios-chatbubbles"
                     >
                         
                     </Button>
                  </template>
               </Table>
            </Col>
         </Row>
      </Row>

      <Modal
         v-model="showComments"
         title=""
         @on-ok="ok"
         @on-cancel="cancel"
      >
         <div v-for="(comment, index) in itemComments" :key="index">
            <div>
               <div class="chat-bubble" style="margin:5px">
                  {{ comment.comment }}
               </div>
               <div style="margin-right:5px;display:inline-block">
                  <Button
                     type="error"
                     size="small"
                     style="margin-left: 5px"
                     @click="removeComment(comment)"
                     icon="ios-trash"
                  >
                  </Button>
               </div>
            </div>
         </div>
      </Modal>
   </div>
</template>

<script>
export default {
   data: () => ({
      show: false,
      allItems: [],
      itemComments: [],
      showComments: false,
      columns12: [
         {
            title: " ",
            key: "itemName",
         },
         {
            title: "",
            key: "itemDescription",
         },
         {
            title: " ",
            key: "itemDate",
         },
         {
            title: "",
            key: "itemQuality",
         },
         {
            title: "",
            key: "itemQuantity",
         },
         {
            title: "",
            key: "categoryName",
         },
         {
            title: " ",
            key: "subName",
         },
         {
            title: " ",
            key: "itemLike",
         },
         {
            title: "",
            slot: "action",
            width: 170,
            align: "center",
         },
      ],
   }),
   created() {
      this.initData();
   },
   methods: {
      initData() {
         let self = this;
         self.$http
            .get(`${this.$store.state.apiUrl}/items`)
            .then((res) => {
               console.log(res.data);
               self.allItems = res.data;
               self.show = true;
            })
            .catch((e) => {
               self.$Message.error({
                  title: "   ",
                  content: "   ",
               });
            });
      },
      ok() {},
      cancel() {},
      removeComment(comment) {
         let self = this;
         self.$http
            .delete(
               `${this.$store.state.apiUrl}/itemComment/${comment.idItemComment}`
            )
            .then((res) => {
               console.log(res.data);
               self.$Message.console.success({
                  title: "  ",
               });
            })
            .catch((e) => {
               self.$Message.console.error({
                  title: " ",
               });
            });
      },
      goToComments(item) {
         let self = this;
         self.$http
            .get(`${this.$store.state.apiUrl}/commentItem/${item.idItem}`)
            .then((res) => {
               console.log(res.data);
               self.itemComments = res.data;
               self.showComments = true;
            })
            .catch((e) => {
               self.$Message.error({
                  title: "   ",
                  content: "   ",
               });
            });
      },
   },
};
</script>

<style lang="scss" scoped>
.chat-bubble {
   color: #ffffff;
   background-color: #5fbdf3;
   display: inline-block;
   padding: 5px 10px;
   border-radius: 20px;
}
</style>
