<template>
   <div :key="keyComponent">
      <Row style="padding:20px" type="flex" justify="end">
         <!-- <Col span="18">
            <Row>
               <Col span="6" class="searchSection">
                  <Input type="text" placeholder="Username">
                     <Icon type="ios-person-outline" slot="prepend"></Icon>
                  </Input>
               </Col>
               <Col span="6" class="searchSection">
                  <Select placeholder="Select your city" clearable>
                     <Option value="beijing">New York</Option>
                     <Option value="shanghai">London</Option>
                     <Option value="shenzhen">Sydney</Option>
                  </Select>
               </Col>
               <Col span="6" class="searchSection">
                  <Select placeholder="Select your city" clearable>
                     <Option value="beijing">New York</Option>
                     <Option value="shanghai">London</Option>
                     <Option value="shenzhen">Sydney</Option>
                  </Select>
               </Col>
               <Col span="6" class="searchSection">
                  <Button type="info" icon="ios-search"></Button>
               </Col>
            </Row>
         </Col> -->
         <Col span="4" class="searchSection">
            <Button type="success" icon="ios-add" @click="showAddItem = true"
               > </Button
            >
         </Col>
      </Row>

      <Row>
         <Col span="24">
            <Table
               border
               :columns="columns12"
               :data="items"
               style="direction:rtl"
            >
               <template slot-scope="{ row }" slot="name">
                  <strong>{{ row.name }}</strong>
               </template>
               <template slot-scope="{row}" slot="offer">
                  <i-switch :value="row.special" :true-value="1" :false-value="0" @on-change="change(row)" />
               </template>
               <template slot-scope="{ row, index }" slot="action">
                  <Button
                     type="primary"
                     size="small"
                     style="margin-left: 5px"
                     @click="goToImage(row)"
                     icon="ios-eye"
                     ></Button>
                  <Button type="error" icon="ios-trash" size="small" @click="remove(row)"></Button>
               </template>
            </Table>
         </Col>
      </Row>

      <Modal title=" " v-model="showAddItem" @on-ok="saveData">
         
         <span style="padding:10px"> </span>
         <Input type="text" placeholder=" " style="padding:10px" v-model="itemName">
         </Input>

         <span style="padding:10px">   (  )</span>
         <Input type="text" placeholder="  (  )" style="padding:10px" v-model="itemNameEn">
         </Input>

         <span style="padding:10px"></span>
         <Input type="textarea" placeholder="" style="padding:10px" v-model="itemDescription">
         </Input>

         <span style="padding:10px"> (  )</span>
         <Input type="textarea" placeholder=" (  )" style="padding:10px" v-model="itemDescriptionEn">
         </Input>
         

         <span style="padding:10px"></span>
         <div style="padding:10px">
         <input type="number" class="type-number-input" v-model="itemQuality" placeholder="" style="width:100%;direction:rtl"></input>
         </div>
         </Input>

         <span style="padding:10px"></span>
         <div style="padding:10px">
         <input type="number" class="type-number-input" v-model="itemQuantity" placeholder=""style="width:100%;direction:rtl"></input>
         </div>
         <div style="padding:10px">
            <Select placeholder=" " v-model="chosenItem"  clearable>
               <Option v-for="(sub , index) in subCategories" :key="index" :value="sub.idSub" >{{sub.subName}} - {{ sub.categoryName}}</Option>
            </Select>
         </div>
      </Modal>


      <Modal title="View Image" v-model="visible">
         <img :src="imgName" v-if="visible" style="width: 100%" />
      </Modal>

      <Modal title="View Image" v-model="showImages">
         <div class="demo-upload-list" v-for="item in uploadList">
            
               <img :src="item.imagePath" />
               <div class="demo-upload-list-cover">
                  <Icon
                     type="ios-eye-outline"
                     @click.native="handleView(item)"
                  ></Icon>
                  <Icon
                     type="ios-trash-outline"
                     @click.native="handleRemove(item)"
                  ></Icon>
               </div>
         </div>
         <Upload
            ref="upload"
            :show-upload-list="false"
            :default-file-list="defaultList"
            :on-success="handleSuccess"
            :format="['jpg', 'jpeg', 'png']"
            :max-size="2048"
            :on-format-error="handleFormatError"
            :on-exceeded-size="handleMaxSize"
            :before-upload="handleBeforeUpload"
            multiple
            type="drag"
            :data="itemId"
            :action="apiUrl + '/addImage'"
            style="display: inline-block;width:58px;"
         >
            <div style="width: 58px;height:58px;line-height: 58px;">
               <Icon type="ios-camera" size="20"></Icon>
            </div>
         </Upload>
      </Modal>
   </div>
</template>

<script>
export default {
   data() {
      return {
         columns12: [
            {
               title: " ",
               key: "itemName",
            },
            {
               title: "",
               key: "itemDescription",
            },
            {
               title: " ",
               key: "itemDate",
            },
            {
               title: "",
               key: "itemQuality",
            },
            {
               title: "",
               key: "itemQuantity",
            },
            {
               title: "",
               key: "categoryName",
            },
            {
               title: " ",
               key: "subName",
            },
            {
               title: " ",
               key: "itemLike",
            },
            {
               title: " ",
               slot: "offer",
               align: "center",
            },

            {
               title: "",
               slot: "action",
               width: 170,
               align: "center",
            },
         ],
         items: [],
         chosenItem : '',
         itemName : '',
         showSubCategories : false,
         itemDescription:'',
         switch1: false,
         itemNameEn :'',
         itemDescriptionEn : '',
         itemQuality : 0,
         itemQuantity : 0.0,
         visible: false,
         showImages: false,
         showAddItem: false,
         keyComponent: 0,

         itemId: {
            itemId: 1,
         },
         defaultList: [],
         categories : [],
         subCategories : [],
         imgName: "",
         visible: false,
         uploadList: [],
         apiUrl: "",
      };
   },
   created() {
      this.apiUrl = this.$store.state.apiUrl;
      this.initData();
   },
   methods: {
      initData(){

         let self = this

         let items = `${self.$store.state.apiUrl}/items`;
         let categories = `${self.$store.state.apiUrl}/categories`;
         let subCategories = `${self.$store.state.apiUrl}/subCategories`;

         function requestItems() {
            return self.$http.get(items);
         }

         function requestCategories() {
            return self.$http.get(categories);
         }

         function requestSubCategories() {
            return self.$http.get(subCategories);
         }

         Promise.allSettled([
            requestItems(),
            requestCategories(),
            requestSubCategories(),
         ]).then((responses) => {
               const responseItems = responses[0];
               const responseCategories = responses[1];
               const responseSubCategories = responses[2];

               self.loading = false;

               self.items =
                  responseItems.status == "fulfilled"
                     ? responseItems.value.data
                     : [];
               self.categories =
                  responseCategories.status == "fulfilled"
                     ? responseCategories.value.data
                     : [];
               self.subCategories =
                  responseSubCategories.status == "fulfilled"
                     ? responseSubCategories.value.data
                     : [];

                     console.log(self.subCategories)

            })
            .catch((errors) => {
               console.log(errors);
            });
      },
      show(row) {
         let self = this;
         self.itemId.itemId = row.idItem;
         self.defaultList = [];

         this.$http
            .get(`${self.apiUrl}/itemImages/${row.idItem}`)
            .then((res) => {
               self.defaultList = res.data;
               self.uploadList = res.data
                  self.showImages = true;
               
            })
            .catch((e) => {
               if (e.response.status == 404) {
                  this.$Message.error({
                     title: "User Info",
                     content: `   `,
                  });
                  self.showImages = true;
               }
            });
      },
      remove(index) {
         let self = this;
         this.items.splice(index, 1);
         self.$http.delete(`${self.$store.state.apiUrl}/item/${index.idItem}`).then(res =>{
            self.$Message.success({
               title : " ",
               content : "  "
            })
            self.initData()
         }).catch(e => {
            console.log(e)
            self.$Message.success({
               title : " ",
               content : "    "
            })
         })
      },
      handleView(name) {
         console.log(name);
         this.imgName = name.imagePath;
         this.visible = true;
      },
      handleRemove(file) {
         let self = this;
         self.$http
            .delete(`${self.apiUrl}/image/${file.idImage}`)
            .then((res) => {
               this.uploadList = this.uploadList.filter((image) => {
                  return image.idImage != file.idImage;
               });

               this.keyComponent += 1;
            })
            .catch((e) => {
               self.$Message.error({
                  title: " ",
                  content: `         `,
               });
            });
      },
      handleSuccess(res, file) {
         console.log(res);
         file.imagePath = res.imagePath;
      },
      handleFormatError(file) {
         this.$Notice.warning({
            title: "   ",
            desc:
               "File format of " +
               file.name +
               " is incorrect, please select jpg or png.",
         });
      },
      handleMaxSize(file) {
         this.$Notice.warning({
            title: "Exceeding file size limit",
            desc: "File  " + file.name + " is too large, no more than 2M.",
         });
      },
      goToImage(row){
         let self = this;
         this.$http
            .get(`${self.apiUrl}/itemImages/${row.idItem}`)
            .then((res) => {
               self.$store.state.itemImages = res.data;
               self.$store.state.selectedItem = row
              self.$router.push("/image-page");
            })
            .catch((e) => {
               self.$store.state.itemImages = [];
               console.log(row)
               self.$store.state.selectedItem = row
              self.$router.push("/image-page");
               if (e.response.status == 404) {
                  this.$Message.error({
                     title: "User Info",
                     content: `   `,
                  });
               }
            });
      },
      change (item) {
         let self = this;
         item.special = item.special == 1 ? 0 : 1; 
         self.$http.put(`${self.apiUrl}/item/${item.idItem}` ,item ).then(res => {
            console.log(res.data)
         })
         self.$Message.info("   ");
      },
      saveData(){
         let self = this;

         console.log(self.chosenItem)
         
         if(self.itemName && self.itemDescription && self.itemQuality && self.itemQuantity && self.chosenItem && self.itemNameEn && self.itemDescriptionEn){
            
            let data = {
               subId : self.chosenItem,
               itemName: self.itemName,
               itemNameEn : self.itemNameEn,
               itemDescriptionEn : self.itemDescriptionEn,
               itemDescription: self.itemDescription,
               itemQuality: self.itemQuality,
               itemQuantity: self.itemQuantity,
               special : 0
            }
            self.$http.post(`${self.apiUrl}/addItem` , data).then(res => {
               self.$Message.success({
                  title : " ",
                  content : "  "
               })
               this.initData();
            }).catch(e => {
               self.$Message.error({
                  title : " ",
                  content : "    "
               })   
            })


         }else{
            self.$Message.error({
               title : " ",
               content : "     "
            })
         }
      },
      handleBeforeUpload() {
         const check = this.uploadList.length < 5;
         if (!check) {
            this.$Notice.warning({
               title: "Up to five pictures can be uploaded.",
            });
         }
         return check;
      },
   },
};
</script>

