<template>
   <div :key="keyComponent">
      <Row>
         <Col>
            <img src="../assets/one.png" width="200px" />
            <Divider></Divider>
         </Col>
      </Row>

      <Row type="flex" justify="end">
         <Col span="6">
            <Button
               type="success"
               icon="ios-add"
               @click="showAddCategory = true"
               > </Button
            >
         </Col>
      </Row>

      <Row v-if="show">
         <Col
            span="6"
            v-for="(category, index) in allCategories"
            :key="index"
            style="padding:10px"
         >
            <Card style="width:320px">
               <div style="text-align:center" @click="goToSub(category)">
                  <img
                     :src="category.categoryImage"
                     width="300"
                     height="150"
                     style="padding: 0 10px"
                  />
                  <!-- <Row>
                        <Col></Col>
                        <Col></Col>
                     </Row> -->
                  <div>
                     <Row
                        style="text-align:right; border:1px solid #eee;padding:5px;border-radius:5px;margin:1px"
                     >
                        <Col span="12">
                           <h3>{{ category.categoryName }}</h3>
                        </Col>
                        <Col span="12">
                           <h3> </h3>
                        </Col>
                     </Row>
                  </div>
                  <div>
                     <Row
                        style="text-align:right; border:1px solid #eee;padding:5px;border-radius:5px;margin:1px"
                     >
                        <Col span="12">
                           <h3>{{ category.categoryNameEn }}</h3>
                        </Col>
                        <Col span="12">
                           <h3>  (  )</h3>
                        </Col>
                     </Row>
                  </div>
               </div>
               <Row style="margin-top:10px">
                  <Col span="12">
                     <Button
                        type="error"
                        icon="ios-trash"
                        @click="deleteCategory(category)"
                        > </Button
                     >
                  </Col>
                  <Col span="12">
                     <!-- <Button
                        type="success"
                        icon="ios-create"
                        @click="editCategory(category)"
                     >
                         
                     </Button> -->
                  </Col>
               </Row>
            </Card>
         </Col>
      </Row>

      <Modal title=" " v-model="showAddCategory" @on-ok="ok">
         <span style="padding:10px"> </span>
         <Input
            type="text"
            placeholder=" "
            style="padding:10px"
            v-model="categoryName"
         >
         </Input>
         <span style="padding:10px">  (  )</span>
         <Input
            type="text"
            placeholder="  (  )"
            style="padding:10px"
            v-model="categoryNameEn"
         >
         </Input>
         <div style="padding:10px">
            <h4> </h4>
            <Row>
               <Col>
                  <div class="dropbox" id="updateCateImage">
                     <input
                        type="file"
                        id="file"
                        accept="image/*"
                        @change="updateFilesChange($event.target.files, $event)"
                     />

                     <label for="file" class="btn-1"> </label>
                  </div>
               </Col>
            </Row>
         </div>
      </Modal>
   </div>
</template>

<script>
export default {
   data() {
      return {
         allCategories: [],
         showAddCategory: false,
         show: false,
         file: "",
         categoryName: "",
         categoryNameEn: "",
         keyComponent: 0,
      };
   },
   created() {
      this.initData();
   },
   methods: {
      initData() {
         let self = this;
         self.$http
            .get(`${this.$store.state.apiUrl}/categories`)
            .then((res) => {
               console.log(res.data);
               self.allCategories = res.data;
               self.show = true;
            })
            .catch((e) => {
               self.$Message.error({
                  title: "   ",
                  content: "   ",
               });
            });
      },
      editCategory(category) {
         console.log(category);
      },
      goToSub(category) {
         let self = this;

         self.$http
            .get(
               `${self.$store.state.apiUrl}/subCategoryId/${category.idCategory}`
            )
            .then((res) => {
               console.log(res.data);
               self.$store.dispatch("storeSubCategories", {
                  categories: res.data,
                  category: category,
               });
               self.$router.push("/sub");
            })
            .catch((e) => {
               self.$store.dispatch("storeSubCategories", {
                  categories: [],
                  category: category,
               });
               self.$router.push("/sub");
            });
      },
      updateFilesChange(file) {
         let self = this;
         self.file = file[0];
      },
      deleteCategory(category) {
         console.log(category);
         let self = this;
         self.$http
            .delete(
               `${this.$store.state.apiUrl}/category/${category.idCategory}`
            )
            .then((res) => {
               self.show = false;
               this.initData();
               self.show = true;
               self.$Message.success({
                  title: " ",
                  content: " ",
               });
            })
            .catch((e) => {
               self.$Message.error({
                  title: "   ",
                  content: "   ",
               });
            });
      },
      ok() {
         let self = this;

         console.log(this.file);
         if (self.file && self.categoryName && self.categoryNameEn) {
            let category = new FormData();
            category.append("categoryName", self.categoryName);
            category.append("file", self.file);
            category.append("categoryNameEn", self.categoryNameEn);

            self.$http
               .post(`${self.$store.state.apiUrl}/addCategory`, category)
               .then(function(res) {
                  self.$Message.success({
                     title: "  ",
                     content: "  ",
                  });

                  self.initData();
               });
         } else {
            self.$Message.error({
               title: " ",
               content: "          ",
            });
         }
      },
   },
};
</script>

