<template>
   <div>
      <Row>
         <Col>
            <img src="../assets/one.png" width="200px" />
            <Divider></Divider>
         </Col>
      </Row>
      <div style="margin-top:50px"></div>
      <Row type="flex" justify="center">
         <Col span="12">
            <span style="padding:10px"> </span>
            <Input
               type="text"
               placeholder=" "
               v-model="title"
               style="padding:10px"
            ></Input>

            <span style="padding:10px"> </span>
            <Input
               type="textarea"
               style="padding:10px"
               v-model="content"
               :autosize="{ minRows: 2, maxRows: 5 }"
               placeholder=" "
            ></Input>

            <Button type="success" @click="sendNotification">
                
            </Button>
         </Col>
      </Row>
   </div>
</template>

<script>
export default {
   data: () => ({
      title: "",
      content: "",
   }),
   methods: {
      sendNotification() {
         let self = this;
         if (self.title && self.content) {
            self.$http
               .post(`${self.$store.state.apiUrl}/sendNotification`, {
                  title: self.title,
                  content: self.content,
               })
               .then((res) => {
                  self.$Message.success("  ");
                  self.title = "";
                  self.content = "";
               });
         } else {
            self.$Message.error("   ");
         }
      },
   },
};
</script>

<style lang="scss" scoped></style>
