<template>
   <div>
      <Row>
         <Col>
            <img src="../assets/one.png" width="200px" />
            <Divider>
               <h3>
                   
               </h3>
            </Divider>
         </Col>
      </Row>

      <Row v-if="show">
         <Row>
            <Col span="24">
               <Table
                  border
                  :columns="columns12"
                  :data="allItems"
                  style="direction:rtl"
               >
                  <template slot-scope="{ row }" slot="name">
                     <strong>{{ row.name }}</strong>
                  </template>
                  <template slot-scope="{ row, index }" slot="action">
                     <Button
                        type="error"
                        size="small"
                        style="margin-left: 5px"
                        @click="deleteOrder(row)"
                        icon="ios-trash"
                     >
                     </Button>
                  </template>
               </Table>
            </Col>
         </Row>
      </Row>
   </div>
</template>

<script>
export default {
   data: () => ({
      show: false,
      allItems: [],
      itemComments: [],
      showComments: false,
      columns12: [
         {
            title: " ",
            key: "customerName",
         },
         {
            title: " ",
            key: "customerPhone",
         },
         {
            title: "",
            key: "customerOrder",
         },
         {
            title: " ",
            key: "specialDate",
         },
         {
            title: "",
            slot: "action",
            width: 170,
            align: "center",
         },
      ],
   }),
   created() {
      this.initData();
   },
   methods: {
      initData() {
         let self = this;
         self.show = false;
         self.$http
            .get(`${this.$store.state.apiUrl}/specialOrders`)
            .then((res) => {
               console.log(res.data);
               self.allItems = res.data;
               self.show = true;
            })
            .catch((e) => {
               self.$Message.error({
                  title: "   ",
                  content: "   ",
               });
            });
      },
      deleteOrder(order) {
         console.log(order);
         let self = this;
         self.$http
            .delete(
               `${this.$store.state.apiUrl}/specialOrder/${order.idSpecialOrder}`
            )
            .then((res) => {
               console.log(res.data);
               self.$Message.console.success({
                  title: "  ",
               });
               self.initData();
            })
            .catch((e) => {
               self.$Message.error({
                  title: "   ",
                  content: "   ",
               });
            });
      },
   },
};
</script>

<style lang="scss" scoped></style>
