<template>
   <div>
      <Row>
         <Col>
            <Button
               style="float:left;"
               icon="md-arrow-round-back"
               type="error"
               @click="goBack"
            ></Button>
            <img src="../assets/one.png" width="200px" />
            <Divider> </Divider>
         </Col>
      </Row>

      <Row type="flex" justify="end">
         <Col span="6">
            <Button
               type="success"
               icon="ios-add"
               @click="showAddCategory = true"
               >  </Button
            >
         </Col>
      </Row>
      <div :key="keyComponent">
         <Row v-if="show">
            <Col
               span="6"
               v-for="(category, index) in allCategories"
               :key="index"
               style="padding:10px"
            >
               <Card style="width:320px">
                  <div style="text-align:center">
                     <img
                        :src="category.imagePath"
                        width="300"
                        height="150"
                        style="padding: 0 10px"
                     />
                  </div>

                  <Row style="margin-top:10px">
                     <Col>
                        <Button
                           type="error"
                           icon="ios-trash"
                           @click="deleteSlider(category)"
                           > </Button
                        >
                     </Col>
                  </Row>
               </Card>
            </Col>
         </Row>
      </div>
      <Modal title="  " v-model="showAddCategory" @on-ok="ok">
         <div style="padding:10px">
            <h4> </h4>
            <Row>
               <Col>
                  <div class="dropbox" id="updateCateImage">
                     <input
                        type="file"
                        id="file"
                        accept="image/*"
                        @change="updateFilesChange($event.target.files, $event)"
                     />

                     <label for="file" class="btn-1"> </label>
                  </div>
               </Col>
            </Row>
         </div>
      </Modal>
   </div>
</template>

<script>
export default {
   data() {
      return {
         allCategories: [],
         showAddCategory: false,
         apiUrl: "",
         show: false,
         file: "",
         categoryName: "",
         categoryNameEn: "",
         keyComponent: 0,
      };
   },
   created() {
      this.apiUrl = this.$store.state.apiUrl;
      this.initData();
   },
   methods: {
      initData() {
         let self = this;
         self.show = false;
         console.log(self.$store.state.itemImages);
         self.allCategories = self.$store.state.itemImages;
         self.show = true;
      },
      reloadImages() {
         let self = this;

         self.$http
            .get(
               `${this.$store.state.apiUrl}/itemImages/${self.$store.state.selectedItem.idItem}`
            )
            .then((res) => {
               self.$store.state.itemImages = res.data;
               this.initData();
            });
      },
      deleteSlider(slider) {
         console.log(slider);
         let self = this;
         self.$http
            .delete(`${this.$store.state.apiUrl}/image/${slider.idImage}`)
            .then((res) => {
               self.show = false;
               self.show = true;
               self.$Message.success({
                  title: " ",
                  content: " ",
               });
               self.reloadImages();
            })
            .catch((e) => {
               self.$Message.error({
                  title: "   ",
                  content: "   ",
               });
            });
      },
      goBack() {
         this.$router.go(-1);
      },
      updateFilesChange(file) {
         let self = this;
         self.file = file[0];
      },
      ok() {
         let self = this;

         console.log(this.file);
         if (self.file) {
            let slider = new FormData();
            slider.append("file", self.file);
            slider.append("itemId", self.$store.state.selectedItem.idItem);

            self.$http
               .post(`${self.$store.state.apiUrl}/addImage`, slider)
               .then(function(res) {
                  console.log(res);
                  self.$Message.success({
                     title: "  ",
                     content: "  ",
                  });

                  self.reloadImages();
               });
         } else {
            self.$Message.error({
               title: " ",
               content: "          ",
            });
         }
      },
   },
};
</script>

