<template>
   <div :key="keyComponent">
      <Row>
         <Col>
            <Button
               style="float:left;"
               icon="md-arrow-round-back"
               type="error"
               @click="goBack"
            ></Button>
            <img src="../assets/one.png" width="200px" />

            <Divider> </Divider>
         </Col>
      </Row>

      <Row type="flex" justify="end">
         <Col span="6">
            <Button
               type="success"
               icon="ios-add"
               @click="showAddCategory = true"
            >
                 
            </Button>
         </Col>
      </Row>

      <Row>
         <Col
            span="6"
            v-for="(category, index) in allSubCategories"
            :key="index"
            style="padding:10px"
         >
            <Card style="width:320px">
               <div style="text-align:center">
                  <img
                     :src="category.subImage"
                     width="300"
                     height="150"
                     style="padding: 0 10px"
                  />

                  <div>
                     <Row
                        style="text-align:right; border:1px solid #eee;padding:5px;border-radius:5px;margin:1px"
                     >
                        <Col span="8">
                           <h3>{{ category.subName }}</h3>
                        </Col>
                        <Col span="16">
                           <h3>  </h3>
                        </Col>
                     </Row>
                  </div>
                  <div>
                     <Row
                        style="text-align:right; border:1px solid #eee;padding:5px;border-radius:5px;margin:1px"
                     >
                        <Col span="8">
                           <h3>{{ category.subNameEn }}</h3>
                        </Col>
                        <Col span="16">
                           <h3>   (  )</h3>
                        </Col>
                     </Row>
                  </div>
                  <Row style="margin-top:10px">
                     <Col>
                        <Button
                           type="error"
                           icon="ios-trash"
                           @click="deleteSubCategory(category)"
                           >  </Button
                        >
                     </Col>
                  </Row>
               </div>
            </Card>
         </Col>
      </Row>

      <Modal title=" " v-model="showAddCategory" @on-ok="ok">
         <span style="padding:10px">  </span>
         <Input
            type="text"
            placeholder="  "
            style="padding:10px"
            v-model="categoryName"
         >
         </Input>
         <span style="padding:10px">   (  )</span>
         <Input
            type="text"
            placeholder="   (  )"
            style="padding:10px"
            v-model="categoryNameEn"
         >
         </Input>
         <div style="padding:10px">
            <h4>  </h4>
            <Row>
               <Col>
                  <div class="dropbox" id="updateCateImage">
                     <input
                        type="file"
                        id="file"
                        accept="image/*"
                        @change="updateFilesChange($event.target.files, $event)"
                     />

                     <label for="file" class="btn-1"> </label>
                  </div>
               </Col>
            </Row>
         </div>
      </Modal>
   </div>
</template>

<script>
export default {
   data() {
      return {
         allSubCategories: [],
         showAddCategory: false,
         category: "",
         show: false,
         file: "",
         categoryName: "",
         categoryNameEn: "",
         keyComponent: 0,
      };
   },
   created() {
      this.initData();
   },
   methods: {
      initData() {
         let self = this;
         self.allSubCategories = self.$store.state.categories;
         console.log(self.allSubCategories);
         self.category = self.$store.state.category;
      },
      updateFilesChange(file) {
         let self = this;
         self.file = file[0];
      },

      deleteSubCategory(subCategory) {
         console.log(subCategory);
         let self = this;
         self.$http
            .delete(
               `${this.$store.state.apiUrl}/subCategory/${subCategory.idSub}`
            )
            .then((res) => {
               self.show = false;
               this.initData();
               self.show = true;
               self.$Message.success({
                  title: " ",
                  content: " ",
               });
            })
            .catch((e) => {
               self.$Message.error({
                  title: "   ",
                  content: "   ",
               });
            });
      },

      goBack() {
         this.$router.go(-1);
      },
      ok() {
         let self = this;

         console.log(this.file);
         if (self.file && self.categoryName && self.categoryNameEn) {
            let subCategory = new FormData();
            subCategory.append("subName", self.categoryName);
            subCategory.append("file", self.file);
            subCategory.append("subNameEn", self.categoryNameEn);
            subCategory.append("categoryId", self.category.idCategory);

            self.$http
               .post(`${self.$store.state.apiUrl}/addSubCategory`, subCategory)
               .then(function(res) {
                  self.$Message.success({
                     title: "  ",
                     content: "  ",
                  });

                  self.initData();
               });
         } else {
            self.$Message.error({
               title: " ",
               content: "          ",
            });
         }
      },
   },
};
</script>

