import Vue from "vue";
import Vuex from "vuex";

Vue.use(Vuex);
//http://dashboard.hayderalkhafaje.com/api
// apiUrl: "http://hayder-alkhafaje.com/api",
// apiUrl: "http://localhost:3110/api",

export default new Vuex.Store({
   state: {
      apiUrl: "http://hayder-alkhafaje.com/api",
      // apiUrl: "http://localhost:3110/api",
      categories: [],
      category: {},
      itemImages: [],
      selectedItem: "",
   },
   mutations: {
      SET_SUB_CATEGORY(state, categories) {
         state.categories = categories.categories;
         state.category = categories.category;
      },
   },
   actions: {
      storeSubCategories({ commit }, categories) {
         commit("SET_SUB_CATEGORY", categories);
      },
   },
   modules: {},
});
