<template>
   <div :key="keyComponent">
      <Row>
         <Col>
            <img src="../assets/one.png" width="200px" />
            <Divider></Divider>
         </Col>
      </Row>

      <Row type="flex" justify="end">
         <Col span="6">
            <Button
               type="success"
               icon="ios-add"
               @click="showAddLocation = true"
               > </Button
            >
         </Col>
      </Row>

      <Row v-if="show">
         <Col
            span="6"
            v-for="(location, index) in allLocations"
            :key="index"
            style="padding:10px"
         >
            <Card style="width:320px">
               <div style="text-align:center">
                  <img
                     :src="location.storeImage"
                     width="300"
                     height="150"
                     style="padding: 0 10px"
                  />

                  <div>
                     <Row
                        style="text-align:right; border:1px solid #eee;padding:5px;border-radius:5px;margin:1px"
                     >
                        <Col span="12">
                           <h3>{{ location.lang }}</h3>
                        </Col>
                        <Col span="12">
                           <h3> </h3>
                        </Col>
                     </Row>
                  </div>
                  <div>
                     <Row
                        style="text-align:right; border:1px solid #eee;padding:5px;border-radius:5px;margin:1px"
                     >
                        <Col span="12">
                           <h3>{{ location.lat }}</h3>
                        </Col>
                        <Col span="12">
                           <h3> </h3>
                        </Col>
                     </Row>
                  </div>
                  <div>
                     <Row
                        style="text-align:right; border:1px solid #eee;padding:5px;border-radius:5px;margin:1px"
                     >
                        <Col span="12">
                           <h3>{{ location.note }}</h3>
                        </Col>
                        <Col span="12">
                           <h3> </h3>
                        </Col>
                     </Row>
                  </div>
                  <div>
                     <Row
                        style="text-align:right; border:1px solid #eee;padding:5px;border-radius:5px;margin:1px"
                     >
                        <Col span="12">
                           <h3>{{ location.noteEn }}</h3>
                        </Col>
                        <Col span="12">
                           <h3>  (  )</h3>
                        </Col>
                     </Row>
                  </div>
                  <Row style="margin-top:10px">
                     <Col>
                        <Button
                           type="error"
                           icon="ios-trash"
                           @click="deleteLocation(location)"
                           > </Button
                        >
                     </Col>
                  </Row>
               </div>
            </Card>
         </Col>
      </Row>

      <Modal title=" " v-model="showAddLocation" @on-ok="ok">
         <span style="padding:10px"> </span>
         <Input
            type="text"
            placeholder=" "
            style="padding:10px"
            v-model="note"
         >
         </Input>
         <span style="padding:10px">  (  )</span>
         <Input
            type="text"
            placeholder="  (  )"
            style="padding:10px"
            v-model="noteEn"
         >
         </Input>
         <span style="padding:10px"> </span>
         <Input
            type="text"
            placeholder=" "
            style="padding:10px"
            v-model="lang"
         >
         </Input>
         <span style="padding:10px"> </span>
         <Input
            type="text"
            placeholder=" "
            style="padding:10px"
            v-model="lat"
         >
         </Input>
         <div style="padding:10px">
            <h4> </h4>
            <Row>
               <Col>
                  <div class="dropbox" id="updateCateImage">
                     <input
                        type="file"
                        id="file"
                        accept="image/*"
                        @change="updateFilesChange($event.target.files, $event)"
                     />

                     <label for="file" class="btn-1"> </label>
                  </div>
               </Col>
            </Row>
         </div>
      </Modal>
   </div>
</template>

<script>
export default {
   data() {
      return {
         allLocations: [],
         showAddLocation: false,
         show: false,
         file: "",
         note: "",
         lang: "",
         lat: "",
         noteEn: "",
         keyComponent: 0,
      };
   },
   created() {
      this.initData();
   },
   methods: {
      initData() {
         let self = this;
         self.$http
            .get(`${this.$store.state.apiUrl}/locations`)
            .then((res) => {
               console.log(res.data);
               self.allLocations = res.data;
               self.show = true;
            })
            .catch((e) => {
               self.$Message.error({
                  title: "   ",
                  content: "   ",
               });
            });
      },

      deleteLocation(location) {
         console.log(location);
         let self = this;
         self.$http
            .delete(
               `${this.$store.state.apiUrl}/location/${location.idLocation}`
            )
            .then((res) => {
               self.show = false;
               this.initData();
               self.show = true;
               self.$Message.success({
                  title: " ",
                  content: " ",
               });
            })
            .catch((e) => {
               self.$Message.error({
                  title: "   ",
                  content: "   ",
               });
            });
      },

      updateFilesChange(file) {
         let self = this;
         self.file = file[0];
      },
      ok() {
         let self = this;

         if (self.file && self.lang && self.lat && self.note && self.noteEn) {
            let location = new FormData();
            location.append("lang", self.lang);
            location.append("lat", self.lat);
            location.append("file", self.file);
            location.append("note", self.note);
            location.append("noteEn", self.noteEn);

            self.$http
               .post(`${self.$store.state.apiUrl}/addLocation`, location)
               .then(function(res) {
                  self.$Message.success({
                     title: "  ",
                     content: "  ",
                  });

                  self.initData();
               });
         } else {
            self.$Message.error({
               title: " ",
               content: "          ",
            });
         }
      },
   },
};
</script>

